// src/App.js
import React, { useState } from 'react';
import { connectWallet } from './ethereum';
import Header from './components/Header';
import LoanForm from './components/LoanForm';

function App() {
  const [account, setAccount] = useState(null);

  const handleConnectWallet = async () => {
    const { signer } = await connectWallet();
    if (signer) {
      const address = await signer.getAddress();
      setAccount(address);
    }
  };

  const createLoan = (amount, interestRate, duration) => {
    // Implement the logic to interact with the smart contract
    console.log('Creating loan:', { amount, interestRate, duration });
  };

  return (
    <div>
      <Header connectWallet={handleConnectWallet} account={account} />
      <LoanForm createLoan={createLoan} />
    </div>
  );
}

export default App;